<template>
  <!--<div class="information" :class="{ linkable }" @click="onclick">-->
  <div class="information" :class="{linkable}" @click="dialogVisible=true">
    <div class="details">
      <span class="player">{{ audio.player_name }}</span>
      <span class="title">{{ audio.title_name }}</span>
      <span v-if="audio.show_name" class="show">{{ audio.show_name }}</span>


    
  <el-dialog v-model="dialogVisible" title="Details" width="500" :before-close="handleClose">
    <span>No.{{ audio.audio_id }}</span>  
    <span>{{ audio.player_name }}</span>
      <span>「{{ audio.title_name }}」</span>
      <span v-if="audio.show_name">{{ audio.show_name }}</span>
      <span v-if="audio.memo">memo: {{ audio.memo }}</span>
    <template #footer>
      <div class="dialog-footer">
        <el-button @click="dialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="onclick">音源を聴く</el-button>
      </div>
    </template>
  </el-dialog>


    </div>

  </div>
</template>


<script>
import { ref } from 'vue'
import { ElMessageBox } from 'element-plus'

export default {
  name: 'AudioInfo',
  props: {
    linkable: { type: Boolean, default: false },
    audio: { type: Object, required: true }
  },
  setup() {
    const dialogVisible = ref(false)

    const handleClose = (done) => {
      ElMessageBox.confirm('このポップアップを閉じます')
        .then(() => {
          done()
        })
        .catch(() => {
          // catch error
        })
    }

    return {
      dialogVisible,
      handleClose
    }
  },
  methods: {
    onclick() {
      // リンクの処理
      window.location.href = this.audio.link;
    }
  }
}
</script>

<style scoped>
.linkable:hover {
  cursor: pointer;
  background-color: #F0F8FF;
}

.details {
  padding: 5px;
}

.details span {
  display: block;
  margin: 5px 0;
}

.information {
  width: 200px;
  margin: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  box-sizing: border-box; 
  background-color: #C7D4D7;
}

.player {
  font-size: 14px;
  color: #333;
  text-align: left;
}

.title {
  font-size: 18px;
  color: #555;
  text-align: center;
  font-weight: 500;
}

.show {
  font-size: 12px;
  color: #777;
  text-align: right;
}


</style>
