<template>
  <div id="register">
    <p>新しいオーディオを登録</p>
    <el-form :model="audio" @submit.prevent="onSubmit">
      <el-form-item label="リンク">
        <el-input v-model="audio.link"></el-input>
      </el-form-item>
      <el-form-item label="演目">
        <el-select v-model="audio.title_id" placeholder="タイトルを選択">
          <el-option v-for="title in titles" :key="title.title_id" :label="title.title_name" :value="title.title_id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="演者">
        <el-select v-model="audio.player_id" placeholder="演者を選択">
          <el-option v-for="player in players" :key="player.player_id" :label="player.player_num+'代目'+player.player_name" :value="player.player_id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="会">
        <el-select v-model="audio.show_id" placeholder="会を選択">
          <el-option v-for="show in shows" :key="show.show_id" :label="show.show_name" :value="show.show_id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="メモ">
        <el-input v-model="audio.memo"></el-input>
      </el-form-item>
      <el-button type="primary" native-type="submit">登録</el-button>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'MemberTab',
  data() {
    return {
      audio: {
        link: '',
        title_id: null,
        player_id: null,
        show_id: null,
        memo: ''
      },
      titles: [],
      players: [],
      shows: []
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      // タイトル、演者、ショーのデータを取得する
      const [titlesResponse, playersResponse, showsResponse] = await Promise.all([
        /*
        fetch('http://localhost:3000/api/titles'),
        fetch('http://localhost:3000/api/players'),
        fetch('http://localhost:3000/api/shows')
        */
        fetch('https://rakken-db-api.onrender.com/api/titles'),
        fetch('https://rakken-db-api.onrender.com/api/players'),
        fetch('https://rakken-db-api.onrender.com/api/shows')
      ]);
      this.titles = await titlesResponse.json();
      this.players = await playersResponse.json();
      this.shows = await showsResponse.json();
    },
    async onSubmit() {
      try {
        const response = await fetch('https://rakken-db-api.onrender.com/api/audios', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(this.audio)
        });
        if (response.ok) {
          alert('オーディオが登録されました');
          this.audio = {
            link: '',
            title_id: null,
            player_id: null,
            show_id: null,
            memo: ''
          };
        } else {
          const errorData = await response.json();
          alert('登録に失敗しました: ' + errorData.message);
        }
      } catch (error) {
        console.error("登録に失敗しました:", error);
        alert('登録に失敗しました');
      }
    }
  }
};
</script>
