<template>
  <div id="register">
    <p>新しい会を登録</p>
    <el-form @submit.prevent="onSubmit">
      <el-form-item label="会の名前">
        <el-input v-model="show_name" placeholder="input new show name here"></el-input>
      </el-form-item>
      <el-form-item label="日付">
      <span class="demonstration"></span>
      <el-date-picker
        v-model="show_date"
        type="date"
        placeholder="Pick a day"
        :size="size"
      />
      </el-form-item>
      <p> {{ show_date }}</p>
      <el-form-item label="場所">
        <el-input v-model="show_location" placeholder="input the show's location here"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">登録</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'ShowTab',
  data() {
    return {
      show_name: '',
      show_date: '',
      show_location: '',
    };
  },
  created() {
  },
  methods: {
    async onSubmit() {
      if (!this.show_name.trim()) {
        alert('演者を入力してください。');
        return;
      }

      let formattedDate;

      if (this.show_date instanceof Date) {
        formattedDate = this.show_date.toISOString().substring(0, 10);
      } else {
        // 文字列として扱う場合は、そのまま処理する
        formattedDate = this.show_date.substring(0, 10);
      }

      try {
//        const response = await fetch('http://localhost:3000/api/shows', {
          const response = await fetch('https://rakken-db-api.onrender.com/api/shows', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ show_name: this.show_name, show_date: formattedDate, show_location: this.show_location})
        });
        if (response.ok) {
          alert('会が登録されました');
          this.show_name = '';
          this.show_date = '';
          this.formattedDate = '';
          this.show_location = '';
        } else {
          const errorData = await response.json();
          alert('登録に失敗しました: ' + errorData.message);
        }
      } catch (error) {
        console.error('登録に失敗しました:', error);
        alert('登録に失敗しました');
      }
    }
  }
};
</script>
