<template>
  <div id="register">
    <p>新しい演者を登録</p>
    <el-form @submit.prevent="onSubmit">
      <el-form-item label="演者">
        <el-input v-model="newPlayer" placeholder="input new player here"></el-input>
      </el-form-item>
      <el-form-item label="代数">
        <el-input v-model="number" placeholder="input player's number here"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">登録</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'PlayerTab',
  data() {
    return {
      newPlayer: '',
      number: '',
    };
  },
  created() {
  },
  methods: {
    async onSubmit() {
      if (!this.newPlayer.trim()) {
        alert('演者を入力してください。');
        return;
      }
      try {
        //const response = await fetch('http://localhost:3000/api/players', {
          const response = await fetch('https://rakken-db-api.onrender.com/api/players', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ player_name: this.newPlayer, player_num: this.number })
        });
        if (response.ok) {
          alert('演者が登録されました');
          this.newPlayer = '';
          this.number ='';
        } else {
          const errorData = await response.json();
          alert('登録に失敗しました: ' + errorData.message);
        }
      } catch (error) {
        console.error('登録に失敗しました:', error);
        alert('登録に失敗しました');
      }
    }
  }
};
</script>
