<template>
  <div id="container">
    <h2>登録する</h2>
    <br>
    <ul>
      <li v-for="tab in tabNames" :key="tab">
        <!-- for文 -->
        <a href="#" v-on:click.prevent="onclick(tab)">{{ tabs[tab] }}</a>
        <!-- .preventでページリロードを回避、引数tabは上のfor文見れば明らか -->
      </li>
    </ul>
    <br>
    <keep-alive>
      <component v-bind:is="currentTabComponent"></component>
    </keep-alive>
  </div>
</template>

<script>
import AudioTab from '@/components/AudioTab.vue';
import TitleTab from '@/components/TitleTab.vue';
import PlayerTab from '@/components/PlayerTab.vue';
import ShowTab from '@/components/ShowTab.vue';

export default {
  name: 'ParentComponent',
  components: {
    AudioTab,
    TitleTab,
    PlayerTab,
    ShowTab
  },
  data() {
    return {
      current: 'audio',
      tabs: {
        audio: '音源',
        title: '演目',
        player: '演者',
        show: '会'
      }
    };
  },
  computed: {
    tabNames() {
      return Object.keys(this.tabs);
    },
    currentTabComponent() {
      const components = {
        audio: 'AudioTab',
        title: 'TitleTab',
        player: 'PlayerTab',
        show: 'ShowTab'
      };
      return components[this.current];
    }
  },
  methods: {
    onclick(tab) {
      this.current = tab;
    }
  }
}
</script>

