<template>
  <div id="register">
    <p>新しい演目を登録</p>
    <el-form @submit.prevent="onSubmit">
      <el-form-item label="演目">
        <el-input v-model="newTitle" placeholder="input new title here"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">登録</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'TitleTab',
  data() {
    return {
      newTitle: '',
    };
  },
  created() {
    /*
    this.fetchTitles();
    */
  },
  methods: {
    /*
    async fetchTitles() {
      try {
        const response = await fetch(process.env.VUE_APP_API_BASE_URL + '/api/titles');
        if (!response.ok) throw new Error('Failed to fetch titles');
        this.titles = await response.json();
      } catch (error) {
        console.error('Error fetching titles:', error);
      }
    },*/
    async onSubmit() {
      if (!this.newTitle.trim()) {
        alert('演目を入力してください。');
        return;
      }
      try {
        //const response = await fetch('http://localhost:3000/api/titles', {
          const response = await fetch('https://rakken-db-api.onrender.com/api/titles', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ title_name: this.newTitle })
        });
        if (response.ok) {
          alert('演目が登録されました');
          this.newTitle = '';
        } else {
          const errorData = await response.json();
          alert('登録に失敗しました: ' + errorData.message);
        }
      } catch (error) {
        console.error('登録に失敗しました:', error);
        alert('登録に失敗しました');
      }
    }
  }
};
</script>
